.hexagon:after {
  background-color: inherit;
  content: "";
  height: inherit;
  position: absolute;
  right: calc((100% / 2) - ((100% * 0.57735) / 2));
  top: 0;
  -webkit-transform: rotateZ(-60deg);
          transform: rotateZ(-60deg);
  width: inherit;
}
.hexagon:before {
  background-color: inherit;
  content: "";
  height: inherit;
  position: absolute;
  right: calc((100% / 2) - ((100% * 0.57735) / 2));
  top: 0;
  -webkit-transform: rotateZ(60deg);
          transform: rotateZ(60deg);
  width: inherit;
}
.hexagon {
  background-color: #008073;
  display: inline-block;
  height: 100%;
  width: calc(100% * 0.57735);
}
.bannerAnim.start div#pt {
